<template class="station-table-content">
<el-table :data="list" border style="width: 100%" height="100%" tooltip-effect="dark" v-loading="false" element-loading-text="...">
          <el-table-column fixed prop="stationName" label="" min-width="70" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="stationId" label="ID" min-width="80" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="clientIp" label="ClientIp" min-width="80" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="clientPort" label="ClientPort" min-width="120" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="b" label="" min-width="144" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="h" label="" min-width="77" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="id" label="ID" min-width="295" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="l" label="" min-width="148" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="location" label="Location" min-width="250" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="mode" label="Mode" min-width="60" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="netId" label="ID" min-width="64" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="x" label="X" min-width="126" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="y" label="Y" min-width="126" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="z" label="Z" min-width="126" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="status" label="" min-width="60" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="serverPort" label="SeverPort" min-width="80" show-overflow-tooltip>
          </el-table-column>
          <el-table-column fixed="right" label="" min-width="50" show-overflow-tooltip>
            <template slot-scope="scope">
              <el-button type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
</template>

<script>
import bus from '@/store/eventbus';
import { BaseInfo } from '@/api/app.js';
export default {
  name: 'basesituation',
  methods: {
    getList() {
      this.listLoading = true;
      // 
      BaseInfo(this.listQuery.page_num, this.listQuery.num_per_page).then(
        response => {
          this.list = response.recordList;
          this.listLoading = false;
        },
        reject => {
          console.log('');
        }
      );
    }
  },
  data() {
    return {
      tabPosition: 'bottom',
      activeName: 'first',
      list: null,
      listLoading: true,
      listQuery: {
        page_num: 1,
        num_per_page: 10
      }
    };
  },
  created() {
    this.getList();
  },
  mounted() {
    // 
    bus.$on('RefreshAfterDeletion', reg => {
      this.getList();
    });
    // 
    bus.$on('RefreshAfterAdd', reg => {
      this.getList();
    });
    bus.$on('RefreshAfterUpdated', reg => {
      this.getList();
    });
  }
};
</script>
<style lang="scss" scoped>

</style>


